import axios from 'axios'
axios.defaults.headers.common['Authorization'] = localStorage.token;

const url = '/api'

class StudentService {
    static async CreateStudent(studentBody) {
        const response = await axios.post(`${url}/students`, studentBody, {
            headers: {
                Authorization: "Bearer " + localStorage.token
            }
        })
        return response.data
    }

    static async DeleteStudent(studentId) {
        const response = await axios.delete(`${url}/students` + studentId)
        return response.data
    }

    static async EnrollStudent(studentIds, offerIds) {
        let promises = []
        let success = ""

        for (let i = 0; i < studentIds.length; i++) {
            const el = studentIds[i];
            promises.push(axios.post(`${url}/students/${el}/enroll`, { offerIds: offerIds }))
        }

        await Promise.all(promises).then(() => success = true).catch(() => success = false)
        return success
    }

    static async GetEnrolleds(studentId) {
        const response = await axios.get(`${url}/students/${studentId}/enrolleds`)
        return response.data;
    }

    static async UpdateEnrolled(enrolledBody) {
        const response = await axios.patch(`${url}/enrolleds/`, enrolledBody);
        return response.data;
    }

    static async GetStudent(studentId) {
        const response = await axios.get(`${url}/students/${studentId}`)
        return response.data;
    }

    static CalculateAssignmentsAverage(assignments) {
        let sum = 0;
        let count = assignments.length;

        for (let i = 0; i < count; i++) {
            sum += assignments[i].mark
        }

        return sum / count
    }

    static async GetEnrolled(studentId, offerId) {
        const response = await axios.get(`${url}/enrolleds/?studentId=${studentId}&offerId=${offerId}`);
        return response.data;
    }
}

export default StudentService;