<template>
  <div v-if="student">
    <v-row>
      <v-col md="6" cols="12">
        <v-card raised shaped dark>
          <v-card-title>
            <v-row class="text-left">
              <v-col cols="12">
                <v-icon class="mx-3">mdi-account</v-icon>
                <span class="font-weight-light">Name:</span>
                <strong> {{ student.firstName }} {{student.middleName}} {{student.lastName}}</strong>
              </v-col>
              <v-col cols="12">
                <v-icon class="mx-3">mdi-medal</v-icon>
                <span class="font-weight-light">Ranking:</span>
                <strong> {{ ranking.rank }} of {{ ranking.size }} </strong>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
      <v-col md="6" cols="12">
        <v-card raised shaped dark>
          <v-card-title>
            <v-row class="text-left">
              <v-col cols="12">
                <v-icon class="mx-3">mdi-book</v-icon>
                <span class="font-weight-light">Homework Ratio:</span>
                <strong>
                  {{ completedAssignmentCount }} of
                  {{ assignmentCount }}
                </strong>
              </v-col>
              <v-col cols="12">
                <v-icon class="mx-3">mdi-calculator</v-icon>
                <span class="font-weight-light">Homework Average:</span>
                <strong
                  :class="{
                    'green--text': average > 50,
                    'red--text': average < 30,
                    'amber--text': average > 30 && average < 50
                  }"
                >
                  {{ average }} %
                </strong>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>
    <v-col cols="4"></v-col>
    <v-row>
      <v-col md="4" cols="12">
        <v-card>
          <v-card-text>
            <v-select
              label="Enrolled"
              outlined
              item-text="firstName"
              item-value="index"
              v-model="selectedEnroll"
              :items="enrolleds"
            >
            </v-select>
            <chartist
              class="ct-chart"
              ratio="ct-major-second"
              type="Line"
              :data="assignmentChartData[selectedEnroll]"
              :options="{
                low: 0,
                high: 100
              }"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col md="4" cols="12">
        <v-card>
          <v-card-text>
            <chartist
              class="ct-chart"
              ratio="ct-major-second"
              type="Bar"
              :data="enrolledChartData"
              :options="{
                high: 100,
                low: 0
              }"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        lg="3"
        md="4"
        sm="6"
        v-for="(enrolled, index) in enrolleds"
        :key="index"
      >
        <v-card raised shaped dark>
          <v-row class="ml-3 text-left">
            <v-col cols="12">
              <v-icon class="mr-3">mdi-book</v-icon>
              {{ enrolled.firstName }}
            </v-col>
            <v-col cols="12">
              <v-icon class="mr-3">mdi-account</v-icon>
              {{ enrolled.teacher.firstName }}
            </v-col>
            <v-col cols="12">
              <v-icon class="mr-3">mdi-calculator</v-icon>
              {{ enrolled.average }} %
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import gql from "graphql-tag";
import StudentService from "../services/StudentService";

export default {
  apollo: {
    student: {
      query: gql`
        query GetStudent($studentId: ID!) {
          student(studentId: $studentId) {
            firstName
            middleName
            lastName
            enrolleds {
              offer {
                course {
                  name
                }
                teacher {
                  firstName
                }
              }
              assignments {
                mark
                homework {
                  name
                }
                isReviewed
              }
            }
          }
        }
      `,
      skip() {
        return this.skipQuery;
      },
      variables() {
        return {
          studentId: this.studentId
        };
      }
    }
  },
  data() {
    return {
      skipQuery: true,
      enrolleds: [],
      assignmentChartData: [],
      enrolledChartData: {},
      selectedEnroll: Number,
      studentId: Number,
      student: Object,
      average: Number,
      ranking: Number,
      assignmentCount: 0,
      completedAssignmentCount: 0,
      assignmentMarks: []
    };
  },

  created() {
    this.studentId = this.$route.params.studentId;
    this.refetchStudent();
  },

  methods: {
    refetchStudent() {
      this.$apollo.queries.student.skip = false;
      this.$apollo.queries.student.refetch();
    }
  },

  watch: {
    student: function(student) {
      let enrolledLabels = [];
      let enrolledSeries = [[]];
      let assignments = [];

      for (let i = 0; i < student.enrolleds.length; i++) {
        const currentEnrolled = student.enrolleds[i];
        let sum = 0;
        let enrolledAverage;
        let enrolledAssignmentCount = currentEnrolled.assignments.length;
        let assignmentDatas = {
          labels: [],
          series: [[]]
        };

        enrolledLabels.push(currentEnrolled.offer.course.firstName);
        this.enrolleds.push({
          firstName: currentEnrolled.offer.course.firstName,
          teacher: currentEnrolled.offer.teacher,
          index: i
        });

        for (let j = 0; j < enrolledAssignmentCount; j++) {
          const currentAssignment = currentEnrolled.assignments[j];

          this.assignmentCount++;
          if (currentAssignment.isReviewed) {
            this.completedAssignmentCount++;
          }

          assignmentDatas.labels.push(currentAssignment.homework.firstName);
          assignmentDatas.series[0].push(currentAssignment.mark);
          sum += currentAssignment.mark;
          assignments.push(currentAssignment);
        }

        enrolledAverage = sum / enrolledAssignmentCount;
        enrolledSeries[0].push(enrolledAverage);

        this.$set(this.assignmentChartData, i, assignmentDatas);
      }

      if (this.enrolleds.length > 0) {
        this.selectedEnroll = 0;
      }

      this.average = StudentService.CalculateAssignmentsAverage(assignments);
      this.$set(this.enrolledChartData, "labels", enrolledLabels);
      this.$set(this.enrolledChartData, "series", enrolledSeries);
      console.log(student);
    }
  }
};
</script>

